import swiperTabHead from "../../components/index/swiper-tab-head.vue"
import loadMore from "../../components/common/load-more.vue"
import noThing from "../../components/common/no-thing.vue"
import topicList from "../../components/news/topic-list.vue"
export default {
	name: "topic-nav-page",
	components: {
		swiperTabHead,
		loadMore,
		noThing,
		topicList
	},
	data() {
		return {
			ischange: false,
			tabIndex: 0,
			tabBars: [],
			newslist: []
		}
	},
	onLoad(e) {
		if (e.ischange) {
			this.ischange = true;
			// 修改页面标题
			uni.setNavigationBarTitle({
				title:"选择所属话题"
			})
		}
		this.getNav();
		
	},
	methods: {
		//获取文章分类
		async getNav(){
			let [err,res] = await this.$http.get('/topicclass');
			if(!this.$http.errorCheck(err,res)) return;
			let list = res.data.data.list;
			let arr = [];
			let arr2= [];
			for (let i=0; i < list.length; i++) {
				arr.push({
					id: list[i].id,
					name: list[i].classname
				});
				arr2.push({
					loadtext:"上拉加载更多",
					lists:[],
					page:1,
					firstload:false
				})
			}
			this.tabBars = arr;
			this.newslist = arr2;
			this.tabBars.length > 0 && this.getList();
		},
		async getList(){
			let url = `/topicclass/${this.tabBars[this.tabIndex].id}/topic/${this.newslist[this.tabIndex].page}`;
			let [err,res] = await this.$http.get(url);
			let error = this.$http.errorCheck(err,res,()=>{
				this.newslist[this.tabIndex].loadtext="上拉加载更多";
			},()=>{
				this.newslist[this.tabIndex].loadtext="上拉加载更多";
			});
			if (!error) return;
			let arr = [];
			let list = res.data.data.list;
			for (let i = 0; i < list.length; i++) {
				arr.push({
					id:list[i].id,
					titlepic:list[i].titlepic,
					title:list[i].title,
					desc:list[i].desc,
					totalnum:list[i].post_count,
					todaynum:list[i].todaypost_count,
				});
			}
			this.newslist[this.tabIndex].lists = this.newslist[this.tabIndex].page > 1 ? this.newslist[this.tabIndex].lists.concat(arr) : arr;
			this.newslist[this.tabIndex].firstload = true;
			if (list.length < 10) {
				this.newslist[this.tabIndex].loadtext="没有更多数据了";
			}else{
				this.newslist[this.tabIndex].loadtext="上拉加载更多";
			}
			return;
		},
		//点击事件
		tabtap(index) {
			this.tabIndex = index;
			this.getList();
		},
		//滑动事件
		tabChange(e) {
			this.tabIndex = e.detail.current;
			this.getList();
		},
		loadmore(index) {
			if(this.newslist[index].loadtext!="上拉加载更多"){ return; }
			// 修改状态
			this.newslist[index].loadtext="加载中...";
			this.newslist[this.tabIndex].page++;
			this.getList();
		}
	}
}
